"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Card, CardContent, CardHeader } from "@/components/ui/card";
import { Label } from "@/components/ui/label";

export default function ClientLoginPage() {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  async function onSubmit(e: React.FormEvent) {
    e.preventDefault();
    setLoading(true);
    setError(null);
    const res = await fetch("/api/auth/login", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ username, password }),
    });
    setLoading(false);
    if (!res.ok) {
      const data = await res.json().catch(() => ({}));
      setError(data?.error ?? "Login failed");
      return;
    }
    location.href = "/dashboard";
  }

  return (
    <div className="min-h-screen flex items-center justify-center p-6">
      <Card className="w-full max-w-xl bg-jsBlack-800 border-white/10 shadow-2xl">
        <CardHeader>
          <div className="flex items-center gap-3">
            <div className="h-10 w-10 rounded-lg bg-jsOrange-500 shadow-lg shadow-jsOrange-500/20" />
            <div>
              <div className="text-white text-2xl font-bold tracking-tight">Client Login</div>
              <div className="text-white/60 text-sm font-medium">Just Search Client CRM</div>
            </div>
          </div>
        </CardHeader>
        <CardContent>
          <form onSubmit={onSubmit} className="space-y-4">
            <div className="space-y-2">
              <Label className="text-white/80">Username</Label>
              <Input 
                value={username} 
                onChange={(e) => setUsername(e.target.value)} 
                placeholder="client@email.com" 
                className="bg-jsBlack-900 border-white/10 text-white placeholder:text-white/30 focus-visible:ring-jsOrange-500 focus-visible:border-jsOrange-500/50"
              />
            </div>
            <div className="space-y-2">
              <Label className="text-white/80">Password</Label>
              <Input 
                type="password" 
                value={password} 
                onChange={(e) => setPassword(e.target.value)} 
                placeholder="••••••••" 
                className="bg-jsBlack-900 border-white/10 text-white placeholder:text-white/30 focus-visible:ring-jsOrange-500 focus-visible:border-jsOrange-500/50"
              />
            </div>
            {error && <div className="text-sm text-red-400 bg-red-400/10 p-3 rounded-md border border-red-400/20">{error}</div>}
            <Button disabled={loading} className="w-full font-semibold h-11 text-base">
              {loading ? "Logging in..." : "Log In"}
            </Button>
            <div className="text-xs text-white/40 text-center pt-4">
              © Just Search Workspace • Privacy Policy • Terms
            </div>
          </form>
        </CardContent>
      </Card>
    </div>
  );
}
